import { Component, OnInit } from '@angular/core';
import { Usermodel }    from '../usermodel';
import { Router } from '@angular/router';
import {FormsModule} from '@angular/forms';
import { UserService } from '../user.service';
@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {
name="";
email="";
college="";
address="";
phonenumber:number;
password="";
mymessage="";
display=false;

  constructor(private _userservice:UserService,private router:Router) { }

  ngOnInit() {
  }
  
  signup(){
    console.log(this.name+"--"+this.email+"--"+this.college+"--"+this.address+"--"+this.password+"--"+this.phonenumber);
    var user:Usermodel={
      name:this.name,
      email:this.email,
      college:this.college,
      address:this.address,
      phonenumber:this.phonenumber,
      password:this.password
    }
      
    
    this._userservice.adduser(user).subscribe(mymsg => {
      this.mymessage=mymsg;
      this.display=true;
    
    }
  );
  }
  onclose(){
    this.display=false;
    if(this.mymessage==='successfully added new user'){
      this.router.navigate(['/login']);
    }
  }


}
